import React, { useState } from 'react';

interface FilterOption {
  label: string;
  value: string;
}

interface SidebarFilterProps {
  genres: FilterOption[];
  types: FilterOption[];
  popularityOptions: FilterOption[];
  ratingOptions: FilterOption[];
  onFilterChange: (filters: {
    genre: string | null;
    type: string | null;
    popularity: string | null;
    rating: string | null;
  }) => void;
}

const SidebarFilter: React.FC<SidebarFilterProps> = ({
  genres,
  types,
  popularityOptions,
  ratingOptions,
  onFilterChange,
}) => {
  const [selectedGenre, setSelectedGenre] = useState<string | null>(null);
  const [selectedType, setSelectedType] = useState<string | null>(null);
  const [selectedPopularity, setSelectedPopularity] = useState<string | null>(null);
  const [selectedRating, setSelectedRating] = useState<string | null>(null);

  const handleChange = (
    filterName: 'genre' | 'type' | 'popularity' | 'rating',
    value: string | null
  ) => {
    switch (filterName) {
      case 'genre':
        setSelectedGenre(value);
        break;
      case 'type':
        setSelectedType(value);
        break;
      case 'popularity':
        setSelectedPopularity(value);
        break;
      case 'rating':
        setSelectedRating(value);
        break;
    }
    onFilterChange({
      genre: filterName === 'genre' ? value : selectedGenre,
      type: filterName === 'type' ? value : selectedType,
      popularity: filterName === 'popularity' ? value : selectedPopularity,
      rating: filterName === 'rating' ? value : selectedRating,
    });
  };

  return (
    <aside className="w-64 bg-gradient-to-tr from-indigo-900 via-blue-800 to-indigo-900 p-6 rounded-lg text-white shadow-lg">
      <h3 className="text-xl font-bold mb-4">Filters</h3>

      <div className="mb-6">
        <label className="block mb-2 font-semibold">Genre</label>
        <select
          className="w-full rounded bg-indigo-800 text-white p-2"
          value={selectedGenre || ''}
          onChange={(e) => handleChange('genre', e.target.value || null)}
        >
          <option value="">All</option>
          {genres.map((g) => (
            <option key={g.value} value={g.value}>
              {g.label}
            </option>
          ))}
        </select>
      </div>

      <div className="mb-6">
        <label className="block mb-2 font-semibold">Type</label>
        <select
          className="w-full rounded bg-indigo-800 text-white p-2"
          value={selectedType || ''}
          onChange={(e) => handleChange('type', e.target.value || null)}
        >
          <option value="">All</option>
          {types.map((t) => (
            <option key={t.value} value={t.value}>
              {t.label}
            </option>
          ))}
        </select>
      </div>

      <div className="mb-6">
        <label className="block mb-2 font-semibold">Popularity</label>
        <select
          className="w-full rounded bg-indigo-800 text-white p-2"
          value={selectedPopularity || ''}
          onChange={(e) => handleChange('popularity', e.target.value || null)}
        >
          <option value="">All</option>
          {popularityOptions.map((p) => (
            <option key={p.value} value={p.value}>
              {p.label}
            </option>
          ))}
        </select>
      </div>

      <div>
        <label className="block mb-2 font-semibold">Rating</label>
        <select
          className="w-full rounded bg-indigo-800 text-white p-2"
          value={selectedRating || ''}
          onChange={(e) => handleChange('rating', e.target.value || null)}
        >
          <option value="">All</option>
          {ratingOptions.map((r) => (
            <option key={r.value} value={r.value}>
              {r.label}
            </option>
          ))}
        </select>
      </div>
    </aside>
  );
};

export default SidebarFilter;
