import React, { useState } from 'react';

interface GameTileProps {
  title: string;
  description: string;
  imageUrl: string;
  onClick?: () => void;
}

const GameTile: React.FC<GameTileProps> = ({ title, description, imageUrl, onClick }) => {
  const [hovered, setHovered] = useState(false);

  return (
    <div
      className="relative w-64 h-40 rounded-lg overflow-hidden cursor-pointer shadow-lg transition-transform transform hover:scale-105"
      onMouseEnter={() => setHovered(true)}
      onMouseLeave={() => setHovered(false)}
      onClick={onClick}
    >
      <img src={imageUrl} alt={title} className="w-full h-full object-cover" />
      {hovered && (
        <div className="absolute inset-0 bg-black bg-opacity-70 flex flex-col justify-center items-center p-4 text-white">
          <h3 className="text-lg font-bold mb-2">{title}</h3>
          <p className="text-sm">{description}</p>
          <button className="mt-4 bg-red-600 hover:bg-red-700 px-4 py-2 rounded transition">
            Play Now
          </button>
        </div>
      )}
    </div>
  );
};

export default GameTile;
