"use client";
import React from 'react';
import { motion } from 'framer-motion';

interface GameCardProps {
  title: string;
  description: string;
  imageUrl: string;
  onClick?: () => void;
}

const GameCard: React.FC<GameCardProps> = ({ title, description, imageUrl, onClick }) => {
  return (
    <motion.div
      whileHover={{ scale: 1.05, boxShadow: '0 0 15px rgba(255, 77, 77, 0.7)' }}
      transition={{ type: 'spring', stiffness: 300 }}
      className="bg-gradient-to-tr from-red-600 to-indigo-900 rounded-lg overflow-hidden cursor-pointer shadow-lg"
      onClick={onClick}
    >
      <img src={imageUrl} alt={title} className="w-full h-48 object-cover" />
      <div className="p-4">
        <h3 className="text-xl font-semibold mb-2">{title}</h3>
        <p className="text-sm text-gray-300">{description}</p>
      </div>
    </motion.div>
  );
};

export default GameCard;
