import React, { useEffect, useRef } from 'react';

const CursorTrail: React.FC = () => {
  const trailRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const trail = trailRef.current;
    if (!trail) return;

    const positions: { x: number; y: number }[] = [];
    const maxPositions = 10;

    const createDot = () => {
      const dot = document.createElement('div');
      dot.style.position = 'absolute';
      dot.style.width = '10px';
      dot.style.height = '10px';
      dot.style.borderRadius = '50%';
      dot.style.background = 'rgba(255, 77, 77, 0.7)';
      dot.style.pointerEvents = 'none';
      dot.style.filter = 'drop-shadow(0 0 5px rgba(255, 77, 77, 0.8))';
      trail.appendChild(dot);
      return dot;
    };

    const dots: HTMLDivElement[] = [];
    for (let i = 0; i < maxPositions; i++) {
      dots.push(createDot());
    }

    const onMouseMove = (e: MouseEvent) => {
      positions.unshift({ x: e.clientX, y: e.clientY });
      if (positions.length > maxPositions) positions.pop();
    };

    const animate = () => {
      dots.forEach((dot, index) => {
        const pos = positions[index];
        if (pos) {
          dot.style.transform = `translate3d(${pos.x}px, ${pos.y}px, 0)`;
          dot.style.opacity = `${(maxPositions - index) / maxPositions}`;
        }
      });
      requestAnimationFrame(animate);
    };

    window.addEventListener('mousemove', onMouseMove);
    animate();

    return () => {
      window.removeEventListener('mousemove', onMouseMove);
      dots.forEach(dot => trail.removeChild(dot));
    };
  }, []);

  return <div ref={trailRef} className="fixed top-0 left-0 w-full h-full pointer-events-none z-50" />;
};

export default CursorTrail;
