"use client";

import React, { useState } from 'react';
import GameCard from '../components/GameCard';
import GameTile from '../components/GameTile';
import { motion } from 'framer-motion';
import CursorTrail from '../components/CursorTrail';
import ThreeBackground from '../components/ThreeBackground';
import CustomPointer from '../components/CustomPointer';
import SidebarFilter from '../components/SidebarFilter';

const featuredGames = [
  {
    title: 'Ludo',
    description: 'Classic board game for family and friends.',
    imageUrl: '/images/ludo.jpg',
    genre: 'board',
    type: 'classic',
    popularity: 'high',
    rating: 4.5,
  },
  {
    title: 'Snake',
    description: 'Nostalgic arcade game with simple controls.',
    imageUrl: '/images/snake.jpg',
    genre: 'arcade',
    type: 'nostalgia',
    popularity: 'medium',
    rating: 4.0,
  },
  {
    title: 'Sudoku',
    description: 'Challenging puzzle game for brain training.',
    imageUrl: '/images/sudoku.jpg',
    genre: 'puzzle',
    type: 'educational',
    popularity: 'medium',
    rating: 4.2,
  },
];

const genres = [
  { label: 'Board', value: 'board' },
  { label: 'Arcade', value: 'arcade' },
  { label: 'Puzzle', value: 'puzzle' },
];

const types = [
  { label: 'Classic', value: 'classic' },
  { label: 'Nostalgia', value: 'nostalgia' },
  { label: 'Educational', value: 'educational' },
];

const popularityOptions = [
  { label: 'High', value: 'high' },
  { label: 'Medium', value: 'medium' },
  { label: 'Low', value: 'low' },
];

const ratingOptions = [
  { label: '4+ Stars', value: '4' },
  { label: '3+ Stars', value: '3' },
  { label: '2+ Stars', value: '2' },
];

export default function Home() {
  const [filters, setFilters] = useState<{
    genre: string | null;
    type: string | null;
    popularity: string | null;
    rating: string | null;
  }>({
    genre: null,
    type: null,
    popularity: null,
    rating: null,
  });

  const filteredGames = featuredGames.filter((game) => {
    if (filters.genre && game.genre !== filters.genre) return false;
    if (filters.type && game.type !== filters.type) return false;
    if (filters.popularity && game.popularity !== filters.popularity) return false;
    if (filters.rating && game.rating < parseFloat(filters.rating)) return false;
    return true;
  });

  return (
    <>
      <ThreeBackground />
      <CursorTrail />
      <CustomPointer />
      <div className="flex min-h-screen bg-gradient-to-tr from-[#ff4d4d] via-[#ffa64d] to-[#6610f2] text-white relative z-10">
        <SidebarFilter
          genres={genres}
          types={types}
          popularityOptions={popularityOptions}
          ratingOptions={ratingOptions}
          onFilterChange={setFilters}
        />
        <main className="flex-1 flex flex-col items-center justify-start p-8">
          <section className="w-full text-center mb-12">
            <h1 className="text-5xl font-extrabold mb-6 drop-shadow-lg">
              Welcome to Gameverse
            </h1>
            <p className="text-xl mb-12 drop-shadow-md">
              The ultimate AI-enhanced game collection web app with cosmic neon vibes.
            </p>
            <div className="space-x-4">
              <button className="bg-red-600 hover:bg-red-700 transition rounded px-6 py-3 font-semibold shadow-lg">
                Explore Games
              </button>
              <button className="bg-blue-600 hover:bg-blue-700 transition rounded px-6 py-3 font-semibold shadow-lg">
                Join Membership
              </button>
            </div>
          </section>

          <section className="w-full">
            <h2 className="text-3xl font-bold mb-6 drop-shadow-neon">Featured Games</h2>
            <motion.div
              className="flex space-x-6 overflow-x-auto scrollbar-hide"
              drag="x"
              dragConstraints={{ left: -300, right: 0 }}
              whileTap={{ cursor: 'grabbing' }}
            >
              {filteredGames.map((game) => (
                <div key={game.title} className="min-w-[300px]">
                  <GameCard
                    title={game.title}
                    description={game.description}
                    imageUrl={game.imageUrl}
                  />
                </div>
              ))}
            </motion.div>
          </section>

          <section className="mt-12 w-full">
            <h2 className="text-3xl font-bold mb-6 drop-shadow-neon">Game Tiles</h2>
            <div className="flex flex-wrap gap-6 justify-center">
              {filteredGames.map((game) => (
                <GameTile
                  key={game.title}
                  title={game.title}
                  description={game.description}
                  imageUrl={game.imageUrl}
                  onClick={() => alert(`Launching ${game.title}...`)}
                />
              ))}
            </div>
          </section>
        </main>
      </div>
    </>
  );
}
